
































import HeadTtop from '../../../components/head/head.vue'

export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {
		return {
			Title: '会员权益',
			backshow: true,
			topsrc: '../../../static/images/back.png',
			price: 2000,
			userinfo: [],
			order_id: '',
		}
	},
	onLoad() {
		this.userinfo = uni.getStorageSync('userinfo')
		if (this.userinfo.is_vip == 2) {
			this.price = 2000
		} else {
			this.price = ''
		}
	},
	methods: {
		getUserino() { // 获取个人信息
			var that = this;
			let params = {
				_token: uni.getStorageSync('userinfo')._token
			}
			this.$http.HttpRequst.requestLogin(true, 'user/userInfo', params, 'POST', res => {
				if (res.code === 200) {
					uni.setStorage({
						key: 'userinfo',
						data: res.data
					});
				}
			})
		},
		addOrder() { // 创建订单
			if (this.price == '') {
				uni.showToast({
					title: '请输入充值金额',
					icon: 'success',
					duration: 800
				});
				return false
			}
			var that = this;
			let params = {
				amount: this.price,
				_token: uni.getStorageSync('userinfo')._token
			}
			that.$http.HttpRequst.request(true, 'order/addVipOrder', params, 'POST', res => {
				that.order_id = res.data.id
				that.SureClick()
			});
		},
		SureClick() {
			var that = this;
			let params = {
				order_id: this.order_id,
				type: 1,
				_token: uni.getStorageSync('userinfo')._token
			}
			uni.showActionSheet({
				itemList: ['微信支付', '支付宝支付'],
				success: function(res) {
					if (res.tapIndex === 0) {
						that.$http.HttpRequst.request(true, 'order/wxPay', params, 'POST', res => { //微信支付
							uni.requestPayment({
								provider: 'wxpay',
								orderInfo: res.data, //订单数据
								success: function(res) {
									uni.showToast({
										title: '支付成功',
										icon: 'success',
										duration: 800,
										success() {
											setTimeout(res => {
												that.getUserino()
											}, 800)
										}
									});
								},
								fail: function(err) {
									uni.showToast({
										title: '支付取消',
										icon: 'success',
										duration: 800
									});
								}
							});
						});
					}
					if (res.tapIndex === 1) {
						that.$http.HttpRequst.request(true, 'alipay/pay', params, 'POST', res => { //支付宝支付
							uni.requestPayment({
								provider: 'alipay',
								orderInfo: res, //订单数据
								success: function(res) {
									uni.showToast({
										title: '支付成功',
										icon: 'success',
										duration: 800,
										success() {
											setTimeout(res => {
												that.getUserino()
											}, 800)
										}
									});
								},
								fail: function(err) {
									uni.showToast({
										title: '支付取消',
										icon: 'success',
										duration: 800
									});
								}
							});
						});
					}
				}
			});
		}
	}
}
